<script>
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import axios from "axios";

export default {
  name: "Home",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      data: undefined,
      products: undefined,
      numOfProducts: undefined,
      brands: undefined,
      loadMoreButton: true
    };
  },
  methods: {
    loadMoreItems(){
      const dataLength = this.data.length;
      const itemsLength = this.products.length;

      if((dataLength - itemsLength) > 12) {
        this.products = this.data.slice(0,(itemsLength+12));
        return;
      }

      if((dataLength - itemsLength) <= 12) {
        this.products = this.data;
        this.loadMoreButton = false;
      }
      
    }
  },
  created: function () {
    const el = this;
    const options = {
      method: "GET",
      url: "https://asos2.p.rapidapi.com/products/v2/list",
      params: {
        offset: "0",
        categoryId: "4209",
        limit: "48",
        store: "US",
        country: "US",
        currency: "USD",
        sort: "freshness",
        lang: "en-US",
        sizeSchema: "US",
      },
      headers: {
        "x-rapidapi-key": "33d05ab973mshbed0cbad31809e8p1670fcjsne0a172ca1188",
        "x-rapidapi-host": "asos2.p.rapidapi.com",
      },
    };

    axios
      .request(options)
      .then(function (response) {
        el.data = response.data.products;
        el.numOfProducts = el.data.length;
        el.brands = response.data.facets[5].facetValues;
        
        el.products = el.data.slice(0,12)
      })
      .catch(function (error) {
        console.error(error);
      });
  },
};
</script>

